      //  анимация при навидкние
let  whiteIcon
cards.addEventListener('mouseover', function(e) {
    let card = e.target.closest('.firstscreen__grid_card')
    if(!card) return
    if(!cards.contains(card)) return
    hover(card)
  })
//   обработчик события на клик
// cards.onclick = function (e) {
//     let card = e.target.closest('.firstscreen__grid_card')
//     if(!card) return
//     if(!cards.contains(card)) return
//     hover(card)
// }
function hover(card) {
    if(whiteIcon) {
       let srcIcon = whiteIcon.querySelector('.firstscreen__grid_icon').src.slice(0, -5)
       whiteIcon.querySelector('.firstscreen__grid_icon').src = srcIcon + '.svg' 
    }
    whiteIcon = card
    let srcIcon = whiteIcon.querySelector('.firstscreen__grid_icon').src.slice(0, -4)
       whiteIcon.querySelector('.firstscreen__grid_icon').src = srcIcon + 'w.svg' 
}


//    Мобильное меню
burger.onclick = function (e) { 
   e.preventDefault()
   // document.getElementById('navmenu').classList.remove('header__navmenu')
   // document.getElementById('navmenu').classList.add('header__navmenu_mobile')
     
   document.getElementById('navMenu').style.display='none'
   document.getElementById('mobileMenu').style.display='block'
   document.getElementById('burger').style.display='none'
   document.getElementById('close').style.display='block'
 }